import { Component, Prop, State, h } from '@stencil/core';

@Component({
  tag: 'ir-input',
  styleUrl: 'ir-input.css',
  shadow: false,
})
export class IrInput {
  @Prop({ reflect: true }) placeholder: string;
  @Prop({ reflect: true, mutable: true }) value: string;
  @Prop({ reflect: true }) type: string;
  @State() currentValue: string;
  @Prop({ reflect: true }) inputStyle: string;

  componentWillLoad() {
    if (this.value !== '') {
      this.currentValue = this.value;
    }
  }

  handleChange(event: Event) {
    this.currentValue = (event.target as HTMLInputElement).value;
    this.value = this.currentValue;
  }
  render() {
    return <input class={this.inputStyle} type={this.type} placeholder={this.placeholder} value={this.value} onInput={this.handleChange.bind(this)} />;
  }
}
